import React, { useEffect } from "react";

import "./sharediarySNS.css";
import kakao from "../../assets/kakaoshare.png";

import link from "../../assets/linkshare.png";

const SharediarySNS = () => {
  const url = window.location.href; // url
  useEffect(() => {
    initKakao(); //
  }, []);

  //  init
  const initKakao = () => {
    if (window.Kakao) {
      const kakao = window.Kakao;
      if (!kakao.isInitialized()) {
        kakao.init("13ce2c4185e1fe0707553109dcf25bb2");
        // kakao.init("process.env.REACT_APP_KAKAO_TOKEN");
      }
    }
  };

  //   
  const shareKakao = () => {
    //       sendDefault  .
    window.Kakao.Link.sendDefault({
      objectType: "feed",
      content: {
        title: "",
        description:
          "# # # # # #  # # # # # ",
        imageUrl: "https://ifh.cc/g/KXnbbS.jpg",
        link: {
          mobileWebUrl: url,
          webUrl: url,
        },
      },
      // social: {
      //   likeCount: 286,
      //   commentCount: 45,
      //   sharedCount: 845,
      // },
      buttons: [
        {
          title: "   , GO GO!",
          link: {
            mobileWebUrl: url,
            webUrl: url,
          },
        },
        // {
        //   title: " ",
        //   link: {
        //     mobileWebUrl: url,
        //     webUrl: url,
        //   },
        // },
      ],
    });
  };

  const doCopy = (text) => {
    //  1.
    if (!document.queryCommandSupported("copy")) {
      return alert("   .");
    }
    //  2.
    const textarea = document.createElement("textarea");
    textarea.value = text;
    textarea.style.top = 0;
    textarea.style.left = 0;
    textarea.style.position = "fixed";
    //  3.
    document.body.appendChild(textarea);
    // focus() ->   
    textarea.focus();
    // select() ->       
    textarea.select();
    //  4.
    document.execCommand("copy");
    //  5.
    document.body.removeChild(textarea);
    alert(" .");
  };
  return (
    <div className="sharesnscomponent">
      <div className="kakaoandclipboard">
        <div className="diary_kakao">
          <img
            id="game_kakao"
            className="shareIcon"
            src={kakao}
            alt="kakaotalk"
            width="65px"
            height="65px"
            onClick={shareKakao}
          />
        </div>
        <div className="gme_link" id="game_link">
          <img
            id="game_link"
            src={link}
            onClick={() => doCopy("http://www.oneto100.shop/diary/game")}
          />
        </div>
      </div>
    </div>
  );
};

export default SharediarySNS;
